/* Font professionali per applicazione giuridica */
@import url('https://fonts.googleapis.com/css2?family=Libre+Baskerville:wght@400;700&family=Source+Sans+Pro:wght@300;400;600;700&display=swap');

/* Import delle migliorie grafiche sottili */
@import url('./styles/enhanced-ui.css');

:root {
  /* Palette colori ITFPLUS - Unificata */
  --primary-color: #1B2A4A;
  --primary-light: #2c4270;
  --primary-lighter: #3d5a8a;
  --primary-dark: #0d1a2e;
  --secondary-color: #A67C52;
  --secondary-light: #c9a77d;
  --accent-color: #FFD700;
  --accent-light: #ffe44d;
  --success-color: #2DD4BF;
  --warning-color: #F59E0B;
  --error-color: #F43F5E;
  --info-color: #38BDF8;
  
  /* Scale di grigio/neutri */
  --background-color: #F9FAFC;
  --card-background: #FFFFFF;
  --text-primary: #0F172A;
  --text-secondary: #475569;
  --text-tertiary: #94A3B8;
  --border-light: #E2E8F0;
  
  /* Dimensioni e spazi - Professionali */
  --border-radius-sm: 2px;
  --border-radius-md: 4px;
  --border-radius-lg: 6px;
  --border-radius-xl: 8px;
  
  /* Ombre e profondità */
  --shadow-sm: 0 1px 2px rgba(0, 0, 0, 0.05);
  --shadow-md: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  --shadow-lg: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  --shadow-xl: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
  
  /* Effetti di glassmorphism */
  --glass-background: rgba(255, 255, 255, 0.25);
  --glass-border: 1px solid rgba(255, 255, 255, 0.18);
  --glass-blur: blur(10px);
  --glass-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
}

body {
  margin: 0;
  font-family: 'Source Sans Pro', -apple-system, BlinkMacSystemFont, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--background-color);
  color: var(--text-primary);
  line-height: 1.6;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Libre Baskerville', Georgia, serif;
  font-weight: 700;
  letter-spacing: -0.01em;
}

code {
  font-family: 'SFMono-Regular', Consolas, 'Liberation Mono', Menlo, monospace;
}

/* Stili per animazioni evolute */
.fade-in {
  animation: fadeIn 0.5s cubic-bezier(0.33, 1, 0.68, 1);
}

.slide-up {
  animation: slideUp 0.6s cubic-bezier(0.33, 1, 0.68, 1);
}

.scale-in {
  animation: scaleIn 0.5s cubic-bezier(0.33, 1, 0.68, 1);
}

.bounce-in {
  animation: bounceIn 0.8s cubic-bezier(0.34, 1.56, 0.64, 1);
}

.stagger-item {
  opacity: 0;
  transform: translateY(20px);
}

.fade-blur-in {
  animation: fadeBlurIn 0.8s cubic-bezier(0.33, 1, 0.68, 1);
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes slideUp {
  from { transform: translateY(30px); opacity: 0; }
  to { transform: translateY(0); opacity: 1; }
}

@keyframes scaleIn {
  from { transform: scale(0.92); opacity: 0; }
  to { transform: scale(1); opacity: 1; }
}

@keyframes bounceIn {
  0% { transform: scale(0.9); opacity: 0; }
  70% { transform: scale(1.05); opacity: 1; }
  100% { transform: scale(1); opacity: 1; }
}

@keyframes fadeBlurIn {
  from { opacity: 0; filter: blur(10px); }
  to { opacity: 1; filter: blur(0); }
}

/* Stili aggiuntivi per componenti comunemente usati */
.page-container {
  padding-top: 2.5rem;
  padding-bottom: 5rem;
  animation: fadeIn 0.5s ease-in;
}

.page-title {
  font-family: 'Libre Baskerville', Georgia, serif;
  font-weight: 700;
  margin-bottom: 1.5rem;
  position: relative;
  letter-spacing: -0.01em;
  color: var(--primary-color);
}

.page-title::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 0;
  width: 60px;
  height: 3px;
  background: var(--secondary-color);
  border-radius: 0;
}

/* Stili per la versione */
.version-tag {
  font-size: 0.75rem;
  font-weight: 600;
  font-family: 'Space Grotesk', monospace;
  color: var(--text-secondary);
  background-color: rgba(0,0,0,0.05);
  padding: 0.25rem 0.75rem;
  border-radius: 20px;
  letter-spacing: 0.02em;
}

/* Glassmorphism cards e componenti */
.glass-card {
  background: var(--glass-background);
  backdrop-filter: var(--glass-blur);
  -webkit-backdrop-filter: var(--glass-blur);
  border: var(--glass-border);
  border-radius: var(--border-radius-lg);
  box-shadow: var(--glass-shadow);
}

/* Elementi di design moderno */
.modern-card {
  background-color: var(--card-background);
  border-radius: var(--border-radius-lg);
  border: 1px solid var(--border-light);
  overflow: hidden;
  box-shadow: var(--shadow-md);
  transition: all 0.4s cubic-bezier(0.33, 1, 0.68, 1);
}

.modern-card:hover {
  transform: translateY(-8px);
  box-shadow: var(--shadow-lg);
}

/* Buttons professionali */
.modern-button {
  font-family: 'Source Sans Pro', sans-serif;
  font-weight: 600;
  padding: 0.75rem 1.5rem;
  border-radius: var(--border-radius-md);
  transition: all 0.2s ease;
  border: none;
  background: var(--primary-color);
  color: white;
  cursor: pointer;
  box-shadow: var(--shadow-sm);
}

.modern-button:hover {
  background: var(--primary-light);
  box-shadow: var(--shadow-md);
}

/* Stile per truncate di testo */
.text-truncate {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  display: inline-block;
}

/* Scrollbar customizzato moderno */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background: var(--background-color);
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  background: linear-gradient(180deg, var(--primary-lighter), var(--secondary-color));
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(180deg, var(--primary-color), var(--secondary-color));
}

/* Overrides per Material UI - Stile professionale */
.MuiCard-root, 
.MuiPaper-root {
  transition: all 0.2s ease !important;
  border-radius: var(--border-radius-md) !important;
}

.MuiButton-root,
.MuiIconButton-root {
  transition: all 0.2s ease !important;
  border-radius: var(--border-radius-md) !important;
  font-family: 'Source Sans Pro', sans-serif !important;
}

.MuiButton-contained {
  background: var(--primary-color) !important;
  box-shadow: var(--shadow-sm) !important;
  text-transform: none !important;
  font-weight: 600 !important;
  letter-spacing: 0.02em !important;
  padding: 0.6rem 1.5rem !important;
}

.MuiButton-contained:hover {
  background: var(--primary-light) !important;
  box-shadow: var(--shadow-md) !important;
}

.MuiOutlinedInput-root {
  border-radius: var(--border-radius-md) !important;
  font-family: 'Source Sans Pro', sans-serif !important;
}

.MuiTypography-root {
  font-family: 'Source Sans Pro', sans-serif !important;
}

.MuiTypography-h1, 
.MuiTypography-h2, 
.MuiTypography-h3, 
.MuiTypography-h4, 
.MuiTypography-h5, 
.MuiTypography-h6 {
  font-family: 'Libre Baskerville', Georgia, serif !important;
  letter-spacing: -0.01em !important;
  font-weight: 700 !important;
}

/* Responsive design */
@media (max-width: 600px) {
  .page-title {
    font-size: 1.5rem !important;
  }
  
  .modern-button {
    padding: 0.5rem 1rem;
    font-size: 0.9rem;
  }
}

/* Dark mode variables - si attivano con .dark-theme */
.dark-theme {
  --background-color: #0F172A;
  --card-background: #1E293B;
  --text-primary: #F1F5F9;
  --text-secondary: #CBD5E1;
  --text-tertiary: #94A3B8;
  --border-light: #334155;
}
